// import axios from 'axios'

export default {
  name: 'edited',
  props: {},
  data: function data() {
    return {
      loading: false,
      loading_count: 0,
      // origin_design_data: [
      // 	{
      // 		'design_img': require('@/assets/design/design_1.jpg'),
      // 		'integral' : 10,
      // 		'isCollect': false,
      // 		'labels': ['新年', '红包', '对联']
      // 	},
      // 	{
      // 		'design_img': require('@/assets/design/design_2.jpg'),
      // 		'integral' : 10,
      // 		'isCollect': false,
      // 		'labels': ['灯笼', '红包', '对联']
      // 	},
      // 	{
      // 		'design_img': require('@/assets/design/design_3.jpg'),
      // 		'integral' : 10,
      // 		'isCollect': false,
      // 		'labels': ['祝福', '红包', '对联']
      // 	},
      // 	{
      // 		'design_img': require('@/assets/design/design_2.jpg'),
      // 		'integral' : 10,
      // 		'isCollect': false,
      // 		'labels': ['利是', '红包', '对联']
      // 	},
      // 	{
