      // 	},
      // ],
      origin_design_data: []
    };
  },
  created: function created() {},
  mounted: function mounted() {},
  computed: {},
  methods: {
    cout_load_img: function cout_load_img() {
      // 全部load完解除loading
      var me = this;
      this.loading_count += 1; // console.log(this.loading_count)

      if (this.loading_count >= this.origin_design_data.length) {
        this.loading = false;
      }
    },
    download_collection_func: function download_collection_func(path) {
      var me = this;
      bus.$emit('download', path);
    },
    delete_material: function delete_material(index, id) {
      var me = this;
      this.axios.post("/Save/del_save", {
        template_id: id
      }).then(function (data) {
        console.log(data.data);
        me["origin_design_data"].splice(index, 1);
        me.$message('删除成功！');
      }).catch(function (e) {
        console.log(e);
      });
    }
  },
  beforeRouteEnter: function beforeRouteEnter(to, from, next) {
    next(function (vm) {
      vm.axios.get("/Save/find_save_list").then(function (data) {
        console.log(data.data);

        if (data.data.data.length != 0) {
          vm.origin_design_data = data.data.data;
        }
      }).catch(function (e) {
        console.log(e);
      });

      if (vm.origin_design_data.length != 0) {
        vm.loading = true;
      } else {
        vm.loading = false;
      }
    });
  }
};